<template>
  <div class="root">
    <div class="login-container">
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="90px" class="demo-ruleForm login-form">
        <el-form-item label="邮箱" prop="email">
          <el-input type="email" v-model="ruleForm.email" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pass">
          <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="验证">
          <check></check>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
          <el-button @click="resetForm('ruleForm')">注册</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  import check from '@/user/Check.vue'

  export default {
    name: 'login',
    components: {
      check
    },
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.ruleForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      return {
        ruleForm: {
          pass: '',
          email: ''
        },
        rules: {
          pass: [{
            validator: validatePass,
            trigger: 'blur'
          }],
          email: [{
              required: true,
              message: '请输入邮箱地址',
              trigger: 'blur'
            },
            {
              type: 'email',
              required:true,
              message: '请输入正确的邮箱地址',
              trigger: ['blur', 'change']
            }
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style>
  .root {
    width: 100%;
    background: url(../../static/img/login.jpg) no-repeat;
    background-position:center;
    background-size: cover;
    position: absolute;
    height: 67.5rem;
  }

  .login-container {
    background-color: rgba(255, 242, 247, 0.7);
    border-radius: 0.9375rem;
    width: 25rem;
    margin: 0 auto;
    margin-top: 13%;
    padding: 1.25rem 0;
  }

  .login-form {
    width: 80%;
    text-align: center;
  }


  .el-input__inner {
    height: 1.5625rem;
    border-color: transparent;
    background-color: rgba(255, 246, 254, 0.8);
  }
</style>



