import Vue from 'vue'
import Router from 'vue-router'
import myIndex from '@/page/Index'
import posts from '@/components/Posts'
import post from '@/components/Post'
import uploadImg from '@/components/UploadImg'
import tags from '@/components/Tags'
import regist from '@/user/Regist'
import login from '@/user/Login'

Vue.use(Router)
export default new Router({
  routes: [{
      path: '/',
      name: 'index',
      component: myIndex
    },
    {
      path: '/posts/:tag',
      name: 'posts',
      component: posts
    },
    {
      path: '/posts',
      name: 'posts',
      component: posts
    },
    {
      path: '/post/:id',
      name: 'post',
      component: post
    },
    {
      path: '/upload',
      name: 'uploadImg',
      component: uploadImg
    },
    {
      path: '/tags',
      name: 'tags',
      component: tags
    },
    {
      path: '/regist',
      name: 'regist',
      component: regist
    },
    {
      path: '/login',
      name: 'login',
      component: login
    }
  ]
})



