<template>
  <div class="root">
    <header class="headertop">
      <myHeader></myHeader>
    </header>
    <div class="container">
      <div class="left-container">
        <div class="search-container">
          <form @submit.prevent="search" class="search-form">
            <el-autocomplete id="search-input" v-model="inputTag" :fetch-suggestions="querySearchAsync" placeholder="请输入内容"
              @select="handleSelect"></el-autocomplete>
          </form>
        </div>
        <div class="ul-container">
          <p class="tags-name">author</p>
          <ul id="author-tags" class="tags">
            <li>
              <router-link :to="'/posts/'+img.author" class="tag-link" :title="img.author">
                {{img.author}}
              </router-link>
            </li>
          </ul>
          <p class="tags-name">from</p>
          <ul id="from-tags" class="tags">
            <li>
              <router-link :to="'/posts/'+img.from" class="tag-link" :title="img.from">
                {{img.from}}
              </router-link>
            </li>
          </ul>
          <p class="tags-name">character</p>
          <ul id="character-tags" class="tags">
            <li v-for="item in img.characters">
              <router-link :to="'/posts/'+item" class="tag-link" :title="item">
                {{item}}
              </router-link>
            </li>
          </ul>
          <p class="tags-name">tags</p>
          <ul id="all-tags" class="tags">
            <li v-for="item in img.tags">
              <router-link :to="'/posts/'+item" class="tag-link" :title="item">
                {{item}}
              </router-link>
            </li>
          </ul>
        </div>
        <div class="info-container">
          <div class="img-info">ID:{{img.id}}</div>
          <div class="img-info">Size:{{img.size}}</div>
          <div class="img-info">Click:{{img.clicks}}</div>
          <div class="img-info">Source:<a :href="img.source" class="img-info-a" :title="img.source">{{img.source}}</a></div>
          <div class="img-info">Download:<a :href="img.source" class="img-info-a" :title="img.savePath">{{img.savePath}}</a></div>
        </div>
      </div>
      <div class="rigth-container">
        <div class="image-parent-container">
          <div class="post-image">
            <el-image class="image" :src="'/'+img.savePath" :preview-src-list="srcList" alt="" :fit="fit" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import myHeader from '@/page/Header.vue'
  import myFooter from '@/page/Footer.vue'

  export default {
    name: 'post',
    components: {
      myHeader,
      myFooter
    },
    data() {
      return {
        fit: 'scale-down',
        srcList: [
          '/static/img/e8ee2dd3-1022-494d-b7e9-1f5669cd183d.jpg',
          '/static/img/e8ee2dd3-1022-494d-b7e9-1f5669cd183d.jpg'
        ],
        state: '',
        timeout: null,
        hotTags: [],
        imgTags: [],
        searchTags: [],
        inputTag: '',
        img: {
          author: '',
          characters: [],
          clicks: 0,
          from: '',
          id: 0,
          savePath: '',
          sexy: 0,
          size: 0,
          source: '',
          sourceId: 0,
          tags: []
        }
      }
    },
    methods: {
      //搜索标签
      loadAll() {
        this.$axios.get('/api/tags/list?sort=3&size=10').then(response => {
          this.hotTags = this.formatHandle(response.data)
        })
      },
      querySearchAsync(queryString, cb) {
        clearTimeout(this.timeout)
        this.timeout = setTimeout(() => {
          queryString && queryString.replace(/\s/g, '').length != 0 ? this.$axios.get(
              '/api/tags/list?sort=3&size=10&tag=' + queryString)
            .then(response => {
              this.searchTags = this.formatHandle(response.data)
              cb(this.searchTags)
            }) : cb(this.hotTags)
        }, 1000)
      },
      formatHandle(tags) {
        var list = []
        for (let item of tags) {
          list.push({
            value: item.content
          })
        }
        return list
      },
      handleSelect(item) {
        console.log(item);
      },
      //通过TAG搜索图片
      search() {
        this.currentPage = 1
        this.$axios.get('/api/images/posts?sort=2&tag='+this.inputTag)
          .then(response => {
            this.imgList = response.data
          })
      },
      //根据ID搜索图片
      toImgParent() {
        var id = this.$route.params.id
        this.$axios.get('/api/images/posts/' + id).then(response => {
          var temp = response.data
          var temp1 = this.img
          temp1.author = temp.author
          temp1.characters = temp.characters.split(',')
          temp1.clicks = temp.clicks
          temp1.from = temp.from
          temp1.id = temp.id
          temp1.savePath = temp.savePath
          temp1.sexy = temp.sexy
          temp1.size = temp.size
          temp1.source = temp.source
          temp1.sourceId = temp.sourceId
          temp1.tags = temp.tags.split(',')
          this.img = temp1
          console.log(this.img)
        })
      }
    },
    mounted() {
      this.loadAll()
      this.toImgParent()
    }
  }
</script>

<style>
  .root {
    background: url(../../static/img/kyotoanimation.png);
    width: 100%;
    height: 58rem;
  }

  .search-container {
    height: 4%;
    width: 100%;
  }

  .el-autocomplete {
    height: 100%;
    width: 100%;
  }

  .el-input {
    height: 100%;
    width: 100%;
  }

  .search-form {
    height: 100%;
    width: 100%;
  }

  #search-input {
    height: 100%;
    width: 100%;
  }

  .container {
    height: calc(100vh);
    position: relative;
    width: 100%;
    margin: 0;
    padding: 0;
    height: 52rem;
  }

  .left-container {
    background-color: white;
    float: left;
    width: 14%;
    height: 100%;
    display: inline-block;
  }

  .tags-name {
    background-color: rgba(255, 233, 245, 0.5);
    width: 100%;
    height: 1.875rem;
    text-align: center;
    padding-top: 0.5rem;
    margin: 0;
    color: rgba(255, 149, 197, 0.8);
    font-weight: bold;
  }

  .ul-container {
    overflow: scroll;
    overflow-x: hidden;
    height: 56%;
    width: 100%;
  }

  .ul-container::-webkit-scrollbar {
    width: 18px;
  }

  .ul-container::-webkit-scrollbar-track {

    background-color: rgba(255, 198, 206, 0.7);
    -webkit-border-radius: 2em;
    -moz-border-radius: 2em;
    border-radius: 2em;
  }

  .ul-container::-webkit-scrollbar-thumb {
    background-color: rgba(255, 198, 206, 1);
    -webkit-border-radius: 2em;
    -moz-border-radius: 2em;
    border-radius: 2em;
  }

  .tags {
    list-style: none;
    width: 100%;
    padding: 0;
    margin: 0;
  }

  .tags li {
    width: 100%;
    height: 1.875rem;
    transition: background-color 0.4s;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .tags li:hover {
    background-color: rgba(255, 192, 201, 0.5);
  }

  .tags li:hover {
    color: rgba(255, 112, 181, 1.0);
  }

  .tag-link {
    position: relative;
    top: 0.45rem;
    padding-left: 1.25rem;
    text-decoration: none;
    color: rgba(255, 111, 113, 1.0);
    font-weight: 350;
  }

  .info-container {
    position: relative;
    top: 10%;
    height: 30%;
    background-color: white;
    position: relative;
    padding-left: 10%;
  }

  .img-info {
    color: rgba(103, 129, 123, 0.9);
    margin-top: 5%;
    width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .img-info-a {
    color: rgba(255, 111, 113, 1.0);
    text-decoration: none;
  }

  .rigth-container {
    width: 86%;
    height: 100%;
    display: inline-block;
    position: relative;
    margin: 0;
    padding: 0;
  }

  .rigth-container::before {
    content: '';
    position: absolute;
    top: 1.5%;
    width: 100%;
    height: 1em;
    background:
      linear-gradient(135deg, transparent, transparent 45%, pink, transparent 55%, transparent 100%),
      linear-gradient(45deg, transparent, transparent 45%, pink, transparent 55%, transparent 100%);
    background-size: 3em 0.5em;
    background-repeat: repeat-x, repeat-x;
  }

  .image-parent-container {
    width: 100%;
    border-top: pink solid 0.225rem;
    border-bottom: pink solid 0.225rem;
    border-radius: 2.5rem;
    height: 97%;
    background-color: rgba(255, 246, 250, 0.6);
    margin: 0 auto;
    margin-top: 3%;
    text-align: center;
  }

  .post-image {
    height: 80%;
    width: 80%;
    position: relative;
    top: 10%;
    display: inline-block;
  }

  .image {
    width: 100%;
    height: 100%;
  }
</style>



