<template>
  <div class="myheader">
    <h1 class="headerleft">
      <a href="/" class="logo">
        <span>Boo<i class="el-icon-star-on"></i>Ru</span>
      </a>
    </h1>
    <div class="headerright">
      <ul class="headermenu">
        <li class="li-after"><a href="/posts" class="menu-a">
            <span>
              <p class="menu-font">收集<i class="el-icon-film"></i></p>
            </span>
          </a>
        </li>
        <li class="li-after"><a class="menu-a" @click="drawer=true">
            <span>
              <p class="menu-font">上传<i class="el-icon-upload2"></i></p>
            </span>
          </a>
          <el-drawer class="upload_drawer" :visible.sync="drawer" :direction="direction" :before-close="handleClose"
            :withHeader="withHeader">
            <uploadImg class="upload_interface"></uploadImg>
          </el-drawer>
        </li>
        <li class="li-after">
          <el-dropdown class="menu-dropdown">
            <span class="el-dropdown-link">
              <p class="menu-font">
                管理
              </p>
            </span>
            <el-dropdown-menu slot="dropdown" class="my-dropdown-menu">
              <el-dropdown-item icon="el-icon-plus"><a href="/tags">黄金糕</a></el-dropdown-item>
              <el-dropdown-item icon="el-icon-circle-plus">狮子头</el-dropdown-item>
              <el-dropdown-item icon="el-icon-circle-plus-outline">螺蛳粉</el-dropdown-item>
              <el-dropdown-item icon="el-icon-check">双皮奶</el-dropdown-item>
              <el-dropdown-item icon="el-icon-circle-check">蚵仔煎</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </li>
        <li>
          <el-dropdown class="menu-dropdown">
            <span class="el-dropdown-link">
              <p class="menu-font">
                <el-avatar class="user-icon" src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>
              </p>
            </span>
            <el-dropdown-menu slot="dropdown" style="">
              <el-dropdown-item icon="el-icon-plus">黄金糕</el-dropdown-item>
              <el-dropdown-item icon="el-icon-circle-plus">狮子头</el-dropdown-item>
              <el-dropdown-item icon="el-icon-circle-plus-outline">螺蛳粉</el-dropdown-item>
              <el-dropdown-item icon="el-icon-check">双皮奶</el-dropdown-item>
              <el-dropdown-item icon="el-icon-circle-check">蚵仔煎</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import uploadImg from '@/components/UploadImg.vue'

  export default {
    name: 'myHeader',
    components: {
      uploadImg
    },
    data() {
      return {
        drawer: false,
        direction: 'ltr',
        withHeader: false,
        activeIndex: '1',
        activeIndex2: '1'
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }
    }
  }
</script>

<style>
  .el-drawer__body {
    overflow: auto;
  }

  .el-drawer {
    width: 23rem !important;
  } 

  .el-drawer__container ::-webkit-scrollbar {
    display: none;
  }

  body::-webkit-scrollbar {
     display: none;
  }

  .my-dropdown-menu a {
    text-decoration: none;
    color: rgba(15, 1, 50, 0.8);
  }

  .user-icon {
    position: absolute;
    top: -18px;
    width: 4rem;
    height: 4rem;
  }

  .menu-a {
    text-decoration: none;
  }

  .li-after::after {
    content: "";
    background-color: pink;
    width: 0rem;
    height: 0.5rem;
    display: block;
    position: relative;
    top: -1rem;
    transition: width 1s;
  }

  .li-after:hover::after {
    width: 5.2rem;
  }

  .headermenu {
    list-style: none;
    margin-top: 0;
  }

  .headermenu li {
    display: inline-block;
    width: 5.2rem;
    height: 3.3rem;
  }

  .li-after {
    text-align: center;
  }

  .menu-font {
    font-size: 1.5rem;
    font-weight: bold;
    color: lightpink;
    transition: opacity 0.5s;
  }

  .menu-font:hover {
    opacity: 0.7;
  }

  .headerleft {
    margin-left: 2%;
    margin-top: 0.625rem;
    display: inline-block;
  }

  .headerright {
    display: inline-block;
    float: right;
  }

  .myheader {
    background-color: transparent;
    height: 6rem;
    width: 100%;
    transition: background-color 0.4s;
  }

  .myheader:hover {
    background-color: rgba(255, 247, 255, 0.8);
  }

  .logo {
    text-decoration: none;
    color: pink;
    font-size: 3rem;
  }

  .logo:hover i {
    animation-name: logo-rate;
    animation-iteration-count: infinite;
    animation-duration: 6s;
    animation-direction: normal;
    animation-timing-function: linear;
  }

  @keyframes logo-rate {
    from {
      transform: rotate(0deg);
    }

    to {
      transform: rotate(360deg);
    }
  }
</style>



