<template>
  <div class="posts-root">
    <header class="headertop">
      <myHeader></myHeader>
    </header>
    <div class="posts-container">
      <div class="posts-left-container">
        <div class="posts-search-container">
          <form @submit.prevent="search" class="search-form">
            <el-autocomplete id="search-input" v-model="inputTag" :fetch-suggestions="querySearchAsync" placeholder="请输入内容"
              @select="handleSelect"></el-autocomplete>
          </form>
        </div>
        <div class="posts-ul-container">
          <p class="tags-name">tags</p>
          <ul id="all-tags" class="tags">
            <li v-for="item in tags">
              <router-link :to="'/posts/'+item.content" class="tag-link" :title="item.content">
                {{item.content}}
              </router-link>
            </li>
          </ul>
        </div>
      </div>
      <div class="posts-rigth-container">
        <div class="posts-images-container">
          <span class="posts-bg-img" v-for="item in imgList">
            <router-link :to="'/post/'+item.id" class="img-a">
              <div class="img-parent-div"></div>
            </router-link>
            <el-image class="posts-image" :src="'/'+item.savePath" :preview-src-list="srcList" :fit="fit" />
          </span>
          <div class="posts-page-plugins">
            <el-pagination background layout="prev, pager, next" :page-count="count" @current-change="handleCurrentChange"
              :current-page="currentPage">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import myHeader from '@/page/Header.vue'
  import myFooter from '@/page/Footer.vue'

  export default {
    name: 'posts',
    components: {
      myHeader,
      myFooter
    },
    data() {
      return {
        fit: 'scale-down',
        srcList: [
          'static/img/e8ee2dd3-1022-494d-b7e9-1f5669cd183d.jpg',
          'static/img/e8ee2dd3-1022-494d-b7e9-1f5669cd183d.jpg'
        ],
        inputTag: '',
        timeout: null,
        imgList: [],
        count: 12,
        currentPage: 0,
        searchTags: [],
        hotTags: [],
        tags: []
      };
    },
    methods: {
      // 分页事件
      handleCurrentChange(val) {
        this.currentPage = val
        var url=''
        if(this.inputTag.replace(/\s/g, '').length != 0)
        {
          url='/api/images/posts?sort=2&page='+val+'&tag='+this.inputTag
        }
        else{
          url='/api/images/posts?sort=2&page='+val
        }
        this.$axios.get(url)
          .then(response => {
            this.imgList = response.data
          })
      },
      //搜索标签
      loadAll() {
        this.$axios.get('/api/tags/list?sort=3&size=10').then(response => {
          this.hotTags = this.formatHandle(response.data)
        })
      },
      querySearchAsync(queryString, cb) {
        clearTimeout(this.timeout)
        this.timeout = setTimeout(() => {
          queryString && queryString.replace(/\s/g, '').length != 0 ? this.$axios.get(
              '/api/tags/list?sort=3&size=10&tag=' + queryString)
            .then(response => {
              this.searchTags = this.formatHandle(response.data)
              cb(this.searchTags)
            }) : cb(this.hotTags)
        }, 1000)
      },
      formatHandle(tags) {
        var list = []
        for (var item of tags) {
          list.push({
            value: item.content
          })
        }
        console.log(list)
        return list
      },
      handleSelect(item) {
        console.log(item);
      },
      // 通过TAG搜索图片
      search() {
        this.currentPage = 1
        this.$axios.get('/api/images/posts?sort=2&tag='+this.inputTag)
          .then(response => {
            this.imgList = response.data
          })
      },
      getImgsCount(){
        this.$axios.get('/api/images/count?tag=' + this.inputTag).then(response => {
          this.count = Math.ceil(response.data / 18)
        })
      },
    },
    mounted() {
      if (this.$route.params.tag) {
        this.inputTag = this.$route.params.tag
      }
      this.getImgsCount()
      this.handleCurrentChange(1)
      this.$axios.get('/api/tags/list?sort=3&size=20').then(response => {
        this.tags = response.data
      })
      this.loadAll();
    },
    watch: {
      '$route'(to, from) {
        if (this.$route.params.tag) {
          this.inputTag = this.$route.params.tag
        }
        this.getImgsCount()
        this.handleCurrentChange(1)
      }
    }
  }
</script>

<style>
  .posts-root {
    width: 100%;
    background: url(../../static/img/kyotoanimation.png);
    position: relative;
  }

  .posts-search-container {
    height: 4%;
    width: 100%;
  }

  .el-autocomplete {
    height: 100%;
    width: 100%;
  }

  .el-input {
    height: 100%;
    width: 100%;
  }

  .search-form {
    height: 100%;
    width: 100%;
  }

  #search-input {
    height: 100%;
    width: 100%;
  }

  .posts-container {
    position: relative;
    height: 50rem;
  }

  .posts-left-container {
    background-color: white;
    float: left;
    width: 14%;
    height: 100%;
    display: inline-block;
  }

  .tags-name {
    background-color: rgba(255, 233, 245, 0.5);
    width: 100%;
    height: 1.875rem;
    text-align: center;
    padding-top: 0.5rem;
    margin: 0;
    color: rgba(255, 149, 197, 0.8);
    font-weight: bold;
  }

  .posts-ul-container {
    overflow: scroll;
    overflow-x: hidden;
    height: 96%;
    width: 100%;
  }

  .posts-ul-container::-webkit-scrollbar {
    width: 18px;
  }

  .posts-ul-container::-webkit-scrollbar-track {

    background-color: rgba(255, 198, 206, 0.7);
    -webkit-border-radius: 2em;
    -moz-border-radius: 2em;
    border-radius: 2em;
  }

  .posts-ul-container::-webkit-scrollbar-thumb {
    background-color: rgba(255, 198, 206, 1);
    -webkit-border-radius: 2em;
    -moz-border-radius: 2em;
    border-radius: 2em;
  }


  .tags {
    list-style: none;
    width: 100%;
    padding: 0;
    margin: 0;
  }

  .tags li {
    width: 100%;
    height: 1.875rem;
    transition: background-color 0.4s;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .tags li:hover {
    background-color: rgba(255, 192, 201, 0.5);
  }

  .tags li:hover a {
    color: rgba(255, 112, 181, 1.0);
  }

  .tag-link {
    position: relative;
    top: 0.45rem;
    padding-left: 1.25rem;
    text-decoration: none;
    color: rgba(255, 111, 113, 1.0);
    font-weight: 350;
  }

  .posts-rigth-container {
    width: 86%;
    height: 100%;
    display: inline-block;
    top: 0rem;
    overflow: scroll;
    overflow-x: hidden;
  }

  .posts-rigth-container::-webkit-scrollbar {
    width: 0px;
  }

  .posts-rigth-container::before {
    content: '';
    position: absolute;
    top: 1.5%;
    width: 100%;
    height: 1em;
    background:
      linear-gradient(135deg, transparent, transparent 45%, pink, transparent 55%, transparent 100%),
      linear-gradient(45deg, transparent, transparent 45%, pink, transparent 55%, transparent 100%);
    background-size: 3em 0.5em;
    background-repeat: repeat-x, repeat-x;
  }

  .posts-images-container {
    width: 100%;
    margin-top: 18px;
    height: 100%;
    border-top: pink solid 0.225rem;
    border-bottom: pink solid 0.225rem;
    border-radius: 2.5rem;
    background-color: rgba(255, 246, 250, 0.6);
  }

  .img-a {
    color: pink;
    text-decoration: none;
    font-size: 0.5rem;
  }

  .img-parent-div {
    background-color: pink;
    margin: 0 auto;
    width: 85%;
    opacity: 0.5;
    height: 0.7rem;
    transition: all 0.5s;
  }

  .img-parent-div:hover {
    background-color: hotpink;
  }

  .posts-image {
    border: transparent;
    border-radius: 1.875rem;
    width: 100%;
    height: 100%;
    transition: 0.2s;
    box-shadow: 1px 1px 10px rgba(255, 224, 250, 0.6);
  }

  img:hover {
    transform: scale(1.2);
  }

  .posts-bg-img {
    width: calc(10vw);
    height: calc(10vw);
    text-align: center;
    display: inline-block;
    margin: 1.7rem calc(2vw);
    overflow: hidden;
    margin-top: 1.7rem;
  }

  .posts-page-plugins {
    width: 100%;
    text-align: center;
    padding: 0;
    padding-top: 4.25rem;
    bottom: 0rem;
  }

  .el-pagination {
    margin: 0 auto;
    padding: 0;
    left: 0;
    right: 0;
  }
</style>



