<template>
  <div class="root">
    <div class="regist-container">
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="90px" class="demo-ruleForm regist-form">
        <el-form-item prop="email" label="邮箱">
          <el-input v-model="ruleForm.email"></el-input>
        </el-form-item>
        <el-form-item label="用户名" prop="username">
          <el-input type="text" v-model="ruleForm.username" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pass">
          <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="checkPass">
          <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="验证">
          <check :confirmSuccess="checkFirm"></check>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">注册</el-button>
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  import check from '@/user/Check.vue'

  export default {
    name: 'regist',
    components: {
      check
    },
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.ruleForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
            checkFirm:false,
        ruleForm: {
          email: '',
          username: '',
          pass: '',
          checkPass: '',
          check:false
        },
        rules: {
          email: [{
              required: true,
              message: '请输入邮箱地址',
              trigger: 'blur'
            },
            {
              type: 'email',
              message: '请输入正确的邮箱地址',
              trigger: ['blur', 'change']
            }
          ],
          username:[{
            required:true,
            message:'请输入用户名'
          }],
          pass: [{
            required:true,
            validator: validatePass,
            trigger: 'blur'
          }],
          checkPass: [{
            required:true,
            validator: validatePass2,
            trigger: 'blur'
          }]
        }
      };
    },
    methods: {
      submitForm(formName) {
        if(!this.checkFirm)
        {
          alert("拖动滑块验证")
          return false;
        }
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style>
  .root {
    width: 100%;
    background: url(../../static/img/regist.png) no-repeat center;
    background: cover;
    position: absolute;
    height: 67.5rem;
  }

  .regist-container {
    background-color: rgba(255, 242, 247, 0.7);
    border-radius: 0.9375rem;
    width: 25rem;
    margin: 0 auto;
    margin-top: 13%;
    padding: 1.25rem 0;
  }

  .regist-form {
    width: 80%;
    text-align: center;
  }


  .el-input__inner {
    height: 1.5625rem;
    border-color: transparent;
    background-color: rgba(255, 246, 254, 0.8);
  }
</style>



