<template>
  <div class="index-root">
    <div class="index-container">
      <div class="index-top-container" :style="{'background-image':topBgUrl}">
        <header class="index-header-top">
          <myHeader></myHeader>
        </header>
        <div class="search">
          <form @submit.prevent="search">
            <el-autocomplete v-model="inputTag" :fetch-suggestions="querySearchAsync" placeholder="请输入内容" @select="handleSelect"
              :trigger-on-focus="false" id="searchinput" class="inline-input">
              <i slot="prefix" class="el-input__icon el-icon-search searchicon"></i>
            </el-autocomplete>
          </form>
        </div>
        <div id="banner_wave_1"></div>
        <div id="banner_wave_2"></div>
      </div>
      <!-- 热门标签 -->
      <div class="index-bottom-container" id="particles-js">
        <div class="index-bottom-top">
          <i class="el-icon-s-promotion myicon"></i>
          <span class="s-tag left">预览ღ</span>
          <span class="s-tag right">o(*≧▽≦)ツo(*≧▽≦)ツo(*≧▽≦)ツ
          </span>
          <hr class="indexhr bottom-top-hr" />
        </div>
        <div class="index-bottom1">
          <div class="bottom-carousel">
            <el-carousel :interval="4000" type="card">
              <el-carousel-item v-for="item in carouselImg" :key="item.id" class="hot-carousel">
                <el-image :src="item.savePath" fit="contain" class="hot-carousel-img" />
              </el-carousel-item>
            </el-carousel>
          </div>
        </div>

        <div class="index-bottom-top">
          <i class="el-icon-reading myicon"></i>
          <span class="s-tag left">My Articleღ</span>
          <span class="s-tag right">o(*≧▽≦)ツo(*≧▽≦)ツo(*≧▽≦)ツ
          </span>
          <hr class="indexhr bottom-top-hr" />
        </div>
        <div class="index-bottom2">
          <div class="index-chap-left">
            <h2 class="chap-c1">哈哈</h2>
            <p class="chap-c2">正文啊啊啊啊啊啊啊啊啊啊</p>
            <p class="chap-c3">time</p>
            <p class="chap-c4"><a href="#" class="chap-c4-a">......</a></p>
          </div>
          <div class="index-chap-right">
            <el-image src="/static/image/3.jpg" fit="cover" class="hot-carousel-img" />
          </div>
        </div>
        <div class="index-bottom-top">
          <i class="el-icon-star-off myicon"></i>
          <span class="s-tag left">Hot Imageღ</span>
          <span class="s-tag right">o(*≧▽≦)ツo(*≧▽≦)ツo(*≧▽≦)ツ
          </span>
          <hr class="indexhr bottom-top-hr" />
        </div>
        <ul class="list hot-url" v-infinite-scroll="load" infinite-scroll-disabled="disabled" infinite-scroll-distance="10"
          infinite-scroll-delay="400" infinite-scroll-immediate="true">
          <li class="index-list-item">
            <div class="index-hot-imgs">
              <span class="index-bg-img" v-for="img in hotImgs">
                <el-image class="index-hot-img" :src="img.savePath" :preview-src-list="srcList" fit="scale-down" />
              </span>
            </div>
          </li>
        </ul>
        <div class="index-bottom-alert">
          <p v-if="loading" class="imgalert">
            <i class="el-icon-loading"></i>
            加载中......</p>
          <p v-if="noMore" class="imgalert">哎呀，没有更多了(#^.^#)</p>
        </div>
        <!--        <footer class="index-footer">
          <hr class="indexhr bottom-bottom-hr" />
          <myFooter></myFooter>
        </footer> -->
      </div>
    </div>
    <el-backtop target=".index-container" :visibility-height="700" :right="40">
      <i class="el-icon-caret-top"></i>
    </el-backtop>
  </div>
</template>

<script>
  import myHeader from '@/page/Header.vue'
  import myFooter from '@/page/Footer.vue'

  export default {
    name: 'myIndex',
    components: {
      myHeader,
      myFooter
    },
    data() {
      return {
        searchTags: [],
        inputTag: '',
        topBgUrl: '',
        carouselImg: [],
        pages: 1,
        hotImgs: [],
        disabled: false,
        noMore: false,
        loading: false,
        url: 'static/img/e8ee2dd3-1022-494d-b7e9-1f5669cd183d.jpg',
        srcList: [
          'static/img/e8ee2dd3-1022-494d-b7e9-1f5669cd183d.jpg',
          'static/img/e8ee2dd3-1022-494d-b7e9-1f5669cd183d.jpg'
        ]
      };
    },
    methods: {
      //无限滚动加载
      load() {
        this.loading = true
        this.disabled = true
        setTimeout(() => {
          this.$axios.get('/api/images/posts?sort=2&size=12&page=' + this.pages)
            .then(response => {
              if (response.data != null) {
                console.log(response.data)
                this.hotImgs.push.apply(this.hotImgs, response.data)
                console.log(this.hotImgs)
                this.noMore = false
                this.disabled = false
                this.pages += 1
              } else {
                this.noMore = true
                this.disabled = true
              }
            })
          this.loading = false
        }, 1000)
      },
      //搜索框事件
      querySearchAsync(queryString, cb) {
        clearTimeout(this.timeout)
        this.timeout = setTimeout(() => {
          queryString && queryString.replace(/\s/g, '').length != 0 ? this.$axios.get(
              '/api/tags/list?sort=3&size=10&tag=' + queryString)
            .then(response => {
              this.searchTags = this.formatHandle(response.data)
              cb(this.searchTags)
            }) : cb(this.hotTags)
        }, 1000)
      },
      formatHandle(tags) {
        var list = []
        for (let item of tags) {
          list.push({
            value: item.content
          })
        }
        return list
      },
      handleSelect(item) {
        console.log(item);
      },
      //跳转至相关tag页面
      search() {
        this.$router.push({
          path: '/posts/' + this.inputTag,
        })
      },
      getTopBgUrl() {
        return this.$axios.get('/api/images/bg')
      },
      getCarouselImgs() {
        return this.$axios.get('/api/images/posts?sort=2&size=10')
      }
    },
    mounted() {
      this.$axios.all([this.getTopBgUrl(), this.getCarouselImgs()]).then(this.$axios.spread((topBgUrl, carouselImgs) => {
        this.topBgUrl = 'url(' + topBgUrl.data + ')'
        this.carouselImg = carouselImgs.data
      }))
    }
  }
</script>
<style>
  /* 文章部分 */
  .index-bottom2 {
    box-shadow: 0px 0px 80px rgba(245, 212, 217, 0.3);
    border-radius: 0.5rem;
    background-color: white;
    margin-bottom: 2rem;
    width: 100%;
    height: 18rem;
    position: relative;
  }

  .index-chap-left {
    display: inline-block;
    width: 40%;
    float: left;
    color: mediumvioletred;
  }

  .chap-c1 {
    margin-left: 15%;
  }

  .chap-c2 {
    margin-left: 23%;
    font-size: 0.725rem;
  }

  .chap-c3 {
    position: absolute;
    bottom: 2.25rem;
    margin-left: 6%;
  }

  .chap-c4 {
    position: absolute;
    bottom: 0rem;
    margin-left: 10%;
  }

  .chap-c4-a {
    text-decoration: none;
    color: hotpink;
  }

  .index-chap-right {
    float: right;
    display: inline-block;
    width: 60%;
    height: 100%;
  }

  .index-container {
    top: 0px;
    position: relative;
    overflow-x: hidden;
    background-color: white;
  }

  .index-header-top {
    width: 100%;
  }

  .index-top-container {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;
    height: calc(100vh);
    position: relative;
    margin: 0;
    padding: 0;
    top: 0;
  }

  .search {
    position: absolute;
    text-align: center;
    top: 40%;
    width: 100%;
  }

  #searchinput {
    width: 45rem;
    height: 3.5625rem;
    border-color: pink;
    border-width: 0rem;
    border-radius: 2rem;
    background-color: rgba(245, 212, 217, 0.3);
    color: black;
    font-size: 1.25rem;
    transition: all 1s;
    text-align: center;
  }

  #searchinput:focus {
    text-align: left;
    background-color: white;
    padding-left: 3.3rem;
  }

  .searchicon {
    font-size: 2.5rem;
    margin-top: 0.6rem;
    margin-left: 18rem;
    color: pink;
    display: none;
    transition: all 0.8s;
  }

  #searchinput:focus+.el-input__prefix i {
    display: block;
    margin-left: 0.4rem;
  }


  /*  波浪动画  */
  #banner_wave_1 {
    width: auto;
    height: 65px;
    background: url(https://cdn.jsdelivr.net/gh/Ukenn2112/UkennWeb@5.3/wave/wave1.png) repeat-x;
    _filter: alpha(opacity=80);
    position: absolute;
    bottom: 0;
    width: 400%;
    left: -236px;
    z-index: 1;
    opacity: 1;
    transiton-property: opacity, bottom;
    transition-duration: .4s, .4s;
    animation-name: move2;
    animation-duration: 240s;
    animation-fill-mode: backwards;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }

  #banner_wave_2 {
    width: auto;
    height: 80px;
    background: url(https://cdn.jsdelivr.net/gh/Ukenn2112/UkennWeb@5.3/wave/wave2.png) repeat-x;
    _filter: alpha(opacity=80);
    position: absolute;
    bottom: 0;
    width: 400%;
    left: 0;
    z-index: 0;
    opacity: 1;
    transiton-property: opacity, bottom;
    transition-duration: .4s, .4s;
    animation-name: move2;
    animation-duration: 160s;
    animation-fill-mode: backwards;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }

  @keyframes move1 {
    100% {
      background-position: 100% 0;
    }
  }

  @keyframes move2 {
    100% {
      background-position: -100% 0;
    }
  }

  /*  波浪动画 END  */
  /* 下部布局 */
  .index-bottom-container {
    padding-top: 2rem;
    background-color: transparent;
    margin: 0 auto;
    left: 0;
    right: 0;
    width: 48.75rem;
    margin-bottom: 7rem;
    position: absolute;
  }

  .index-bottom-top {
    margin: 0 auto;
    width: 100%;
    margin-bottom: 1.7rem;
  }

  .index-bottom1 {
    box-shadow: 0px 0px 80px rgba(245, 212, 217, 0.3);
    border-radius: 0.5rem;
    background-color: white;
    margin-bottom: 2rem;
    padding-top: 0.625rem;
  }

  /* hr */
  .indexhr {
    /* 渐变 */
    /* background-image: linear-gradient(to right,rgba(0,0,0,0),pink,rgba(0,0,0,0)); */
    border: none;
    border-top: 8px dotted;
    color: pink;
    height: 6px;
    opacity: 0.3;
  }

  .bottom-top-hr {
    width: 100%;
  }

  .bottom-bottom-hr {
    width: 100%;
    margin-top: calc(4vh);
  }

  .myicon {
    font-size: 2rem;
    color: pink;
  }

  .right {
    margin-top: 0.925rem;
    float: right;
    color: black;
    opacity: 0.5;
  }

  .left {
    color: rgba(255, 166, 178, 1.0);
    font-size: 1.5rem;
    font-weight: bold;
  }

  /* 走马灯 */
  .bottom-carousel {
    margin: calc(1vh) 1.25rem;
  }

  .hot-carousel {
    width: 50%;
    height: 100%;
  }

  .hot-carousel-img {
    height: 100%;
    width: 100%;
  }

  .el-carousel__item h3 {
    color: #475669;
    opacity: 1;
    line-height: 100%;
    margin: 0;
  }

  .hot-url {
    box-shadow: 0px 0px 80px rgba(245, 212, 217, 0.4);
    border-radius: 0.5rem;
    background-color: rgba(255, 248, 255, 0.2);
    list-style: none;
    text-align: center;
    margin: 0;
    padding: 0;
  }

  .index-list-item {}

  .index-hot-imgs {
    margin-bottom: calc(5vh);
    margin-top: 1rem;
  }


  .index-container::-webkit-scrollbar {
    width: 0.425rem;
  }

  .index-container::-webkit-scrollbar-track {
    background-color: rgba(255, 187, 197, 1.0);
    -webkit-border-radius: 2em;
    -moz-border-radius: 2em;
    border-radius: 2em;
  }

  .index-container::-webkit-scrollbar-thumb {
    background-color: hotpink;
    -webkit-border-radius: 2em;
    -moz-border-radius: 2em;
    border-radius: 2em;
  }


  /* 分栏间隔布局css */
  .index-bg-img {
    width: calc(10vw);
    height: calc(10vw);
    text-align: center;
    display: inline-block;
    margin: 2rem calc(2vh);
    overflow: hidden;
  }

  .index-hot-img {
    border: transparent;
    border-radius: 1.875rem;
    width: 100%;
    height: 100%;
    box-shadow: 1px 1px 10px rgba(255, 224, 250, 0.6);
  }

  img {
    transition: all 0.8s;
  }

  img:hover {
    transform: scale(1.2);
  }

  .index-bottom-alert {
    text-align: center;
    margin-bottom: calc(3vh);
  }

  .imgalert {
    color: pink;
    font-size: 1.5rem;
    opacity: 0.7;
  }

  /* 底部 */
  .index-footer {
    clear: both;
    position: absolute;
    bottom: 0rem;
    width: 100%;
  }
</style>



